import { useState } from "react";
import { Link } from "react-router-dom";
import { ev_pbv, 승용, rv, bus } from "../data";
import { Prev } from "react-bootstrap/esm/PageItem";

function Cars() {
	let [tab, setTab] = useState(0);

	return (
		<div className="wrap">
			<div className="header"></div>
			<div className="center">
				<ul variant="tabs" defaultActiveKey="link0" className="car-ul">
					<li>
						<Link
							onClick={() => {
								setTab = 0;
							}}
							eventKey="link0"
						>
							EV / PBV
						</Link>
					</li>
					<li>
						<Link
							onClick={() => {
								setTab(1);
							}}
							eventKey="link1"
						>
							승용
						</Link>
					</li>
					<li>
						<Link
							onClick={() => {
								setTab(2);
							}}
							eventKey="link2"
						>
							RV
						</Link>
					</li>
					<li>
						<Link
							onClick={() => {
								setTab(3);
							}}
							eventKey="link3"
						>
							택시 / 버스 / 상용
						</Link>
					</li>
				</ul>
				<div className="bar"></div>
				<CarsContent tab={tab}></CarsContent>
			</div>
		</div>
	);
}

function CarsContent(props) {
	if (props.tab === 0) {
		return (
			<div className="cars">
				{ev_pbv.map((a, i) => {
					return (
						<div className="detail">
							<div className="img-box">
								<img src={ev_pbv[i].img}></img>
							</div>
							<p className="name">{ev_pbv[i].name}</p>
							<p className="price">{ev_pbv[i].price}</p>
						</div>
					);
				})}
			</div>
		);
	}
	if (props.tab === 1) {
		return (
			<div className="cars">
				{승용.map((a, i) => {
					return (
						<div className="detail">
							<div className="img-box">
								<img src={승용[i].img}></img>
							</div>
							<p className="name">{승용[i].name}</p>
							<p className="price">{승용[i].price}</p>
						</div>
					);
				})}
			</div>
		);
	}
	if (props.tab === 2) {
		return (
			<div className="cars">
				{rv.map((a, i) => {
					return (
						<div className="detail">
							<div className="img-box">
								<img src={rv[i].img}></img>
							</div>
							<p className="name">{rv[i].name}</p>
							<p className="price">{rv[i].price}</p>
						</div>
					);
				})}
			</div>
		);
	}
	if (props.tab === 3) {
		return (
			<div className="cars">
				{bus.map((a, i) => {
					return (
						<div className="detail">
							<div className="img-box">
								<img src={bus[i].img}></img>
							</div>
							<p className="name">{bus[i].name}</p>
							<p className="price">{bus[i].price}</p>
						</div>
					);
				})}
			</div>
		);
	}
}

function M_Cars() {
	let [tab, setTab] = useState(0);

	return (
		<div className="m-cars">
			<ul variant="tabs" defaultActiveKey="link0" className="car-ul">
				<li>
					<Link
						onClick={() => {
							setTab = 0;
						}}
						eventKey="link0"
					>
						EV / PBV
					</Link>
				</li>
				<li>
					<Link
						onClick={() => {
							setTab(1);
						}}
						eventKey="link1"
					>
						승용
					</Link>
				</li>
				<li>
					<Link
						onClick={() => {
							setTab(2);
						}}
						eventKey="link2"
					>
						RV
					</Link>
				</li>
				<li>
					<Link
						onClick={() => {
							setTab(3);
						}}
						eventKey="link3"
					>
						택시 / 버스 / 상용
					</Link>
				</li>
			</ul>
			<div className="bar"></div>
			<CarsContent tab={tab}></CarsContent>
		</div>
	);
}

export { Cars, M_Cars };
