const Sorento = () => {
	const [currentImageIndex, setCurrentImageIndex] = useState(0);
	const [isDragging, setIsDragging] = useState(false);
	const startX = useRef(0);
	const currentX = useRef(0);

	const handleMouseDown = (event) => {
		setIsDragging(true);
		startX.current = event.clientX;
	};

	const handleMouseMove = (event) => {
		if (isDragging) {
			currentX.current = event.clientX;
			const diff = currentX.current - startX.current;
			if (Math.abs(diff) > 5) {
				// 스와이프 감도를 조절
				const newIndex = currentImageIndex + (diff > 0 ? 1 : -1);
				setCurrentImageIndex((newIndex + SorentoImages.length) % SorentoImages.length);
				startX.current = currentX.current; // 시작 위치를 다시 설정
			}
		}
	};

	const handleMouseUp = () => {
		setIsDragging(false);
	};

	const handleDragStart = (event) => {
		event.preventDefault();
	};

	useEffect(() => {
		const handleMouseUpGlobal = () => {
			if (isDragging) {
				setIsDragging(false);
			}
		};

		window.addEventListener("mouseup", handleMouseUpGlobal);
		return () => {
			window.removeEventListener("mouseup", handleMouseUpGlobal);
		};
	}, [isDragging]);

	return (
		<div className="image-360-rotation" onMouseDown={handleMouseDown} onMouseMove={handleMouseMove} onMouseUp={handleMouseUp} onMouseLeave={handleMouseUp}>
			<img src={SorentoImages[currentImageIndex]} alt="360 rotation" draggable="false" onDragStart={handleDragStart} className="sorento-img" />
		</div>
	);
};

